<template>
	<div class="modal-layout">
		<div class="darken">
			<div class="new-modal add-crypto-starter-modal">
				<button class="new-modal-back-button" @click="closeModal"><img
						:src="require('@/assets/newImages/white_cross.png')"></button>
				<div class="new-modal-content">
					<div class="new-modal-header"><img :src="require('@/assets/newImages/cryptostarter.svg')">
						<h3 class="new-modal-title">{{ $localizeFilter('crypto_starter_modal_title') }}</h3>
					</div>

					<label class="new-modal-text">{{ $localizeFilter('crypto_starter_modal_text') }}</label>
					<input v-model="result" :placeholder="$localizeFilter('amount_in', ['FENIX'])" type="number"
						class="new-modal-input" :class="{ invalid: v$.result.$error }">
					<small class="error" v-for="(error, index) of v$.result.$errors" :key="index">
						{{ error.$message }}
					</small>

					<div :class="totalValue < 1000000000 ? 'disabled' : ''" class="conditions-block" @click="acceptShoulder">
						<input :class="totalValue < 1000000000 ? 'disabled' : ''" type="checkbox" id="checkbox" class="custom-checkbox"
							v-model="shoulderAccepted" @click="acceptShoulder">
						<label :class="totalValue < 1000000000 ? 'disabled' : ''" for="checkbox" class="conditions-label">{{
							$localizeFilter('shoulder5x') }}</label>
					</div>

					<label :class="totalValue < 1000000000 ? 'disabled' : ''" class="new-modal-text">{{
						$localizeFilter('available_credit_limit') }}</label>
					<div :class="totalValue < 1000000000 ? 'disabled' : ''" class="new-modal-input data-info">{{ creditLimit }}</div>

					<div :class="totalValue < 1000000000 ? 'disabled' : ''" class="modal-warning">
						<img class="warning-label" src="../../assets/newImages/warning-label.svg" alt="">
						<p :class="totalValue < 1000000000 ? 'disabled' : ''" class="window-text warning">{{
							$localizeFilter('credit_limit_hint') }}</p>
					</div>


					<label :class="totalValue < 1000000000 ? 'disabled' : ''" class="new-modal-text">{{ $localizeFilter('total_amount')
					}}</label>
					<div :class="totalValue < 1000000000 ? 'disabled' : ''" class="new-moda-input data-info">{{ formatUSDT(totalValueWithCreditLever) }}</div>

					<div v-if="totalValue > 0" class="result">{{ $localizeFilter('result_crypto_start', [
						`${formatPercent(getPercent)}`]) }}%</div>

					<button class="basic-button" @click="submitHandler()">{{ $localizeFilter('enter_share') }}</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue } from "@vuelidate/validators";

export default defineComponent({
	data() {
		return {
			// rateFenix: 0.9,
			result: '',
			pools: [],
			minAmount: 0,
			shoulderAccepted: true,
			creditLimit: 0,

			totalAmount: 0,
		}
	},
	setup() {
		return { v$: useVuelidate() };
	},
	validations() {
		return {
			result: {
				required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
				numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
				minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount)),
			},
		};
	},
	methods: {
		closeModal(reload) {
			this.$emit('closeModal', reload)
		},
		resultValue() {
			return this.result / (100000000) * 100
		},
		submitHandler() {
			this.v$.$touch();
			if (this.v$.$error) {
				return;
			}
			this.$emit('payFenix', +this.result)
		},
		formatPercent(value) {
			return (value).toFixed(8)
		},
		acceptShoulder() {
			this.shoulderAccepted = !this.shoulderAccepted
		},
	},
	computed: {
		totalAmountInPools() {
			return this.pools.reduce((acc, pool) => acc + Number((pool.balance) || 0), 0)
		},

		avaivalbleCreditLimit() {
			return this.totalAmountInPools * 4
		},

		totalValue() {
			return this.result * 1000000
		},

		totalValueWithCreditLever() {
			return this.totalValue >= 1000000000 ? Math.min(this.totalValue, this.totalAmountInPools) * 4 + this.totalValue: this.totalValue
		},

		getPercent() {
			return this.totalValueWithCreditLever / 1000000 / 100000000 * 100
		},
		
	},
	async mounted() {
		try {
			const response = await this.$store.dispatch('getCryptoStartAvailablePools')
			this.pools = response
		} catch (e) {
			console.log(e)
		}
	},
})
</script>

