<template>
  <div>
    <div class="top-blur"></div>
    <Loader :visible="loading" />
    <div class="app-main-layout" :class="loading ? 'hidden' : 'visible'">
    <Navbar class="navbar" @click="isOpen = !isOpen" />
    <Sidebar v-model="isOpen" :value="isOpen" @closeSidebar="closeSidebar" @forceUpdateAll="forceUpdateAll"/>
    <div class="darken" v-if="isOpen" @click="isOpen = !isOpen"></div>
      <main class="app-content" >
        <div class="app-page">
          <div class="bottom-blur"></div>
          <transition name="collapse">
            <ExchangeBurningSection
            v-if="showExchangeBurnViews"
            :small="true"
            />
          </transition>
          <!-- <button class="basic-button" @click="$store.dispatch('getTestData')">TEST BUTTON</button> -->
          <router-view />
        </div>
      </main>
    </div>
    <!-- <UnderMaintenanceModal /> -->
    <transition-group name="fade" mode="in-out">
    <div class="darken" v-if="showPopup" @click.stop="">
      <BuyCoinPopupWindow :view="'bigCoinLight'" :position="'center'" :coin="popupCoin" :buyCoinLink="null" @buyCoin="buyCoin" @close="showPopup=false"/>
    </div>
    
    <PaymentCoinModule v-if="showPaymentCoinModule" @close="$store.dispatch('setShowPaymentCoinModule', {value: false})"/>
    <PaymentLevelModule v-if="showPaymentLevelModule" @close="$store.dispatch('setShowPaymentLevelModule', {value: false})"/>
    <PaymentCryptostartModule v-if="showPaymentCryptostartModule" @close="$store.dispatch('setShowPaymentCryptostartModule', {value: false})"/>
    <OGCTVideoModal v-if="showOGCTVideoModal" @close="$store.dispatch('setShowOGCTVideoModal', {value: false})"/>
    <!-- <LiquidityVideoModal v-if="showLiquidityVideoModal" @close="showLiquidityVideoModal=false"/> -->
    <PlatformReviewsModal v-if="showPlatformReviewsModal" @close="showPlatformReviewsModal=false"/>
	  <PromoModal v-if="showPromoModal" @closeModal="showPromoModal=false" />
	  <WithdrawOperationConfirmed v-if="showWithdrawOperationConfirmed" @closeModal="showWithdrawOperationConfirmed=false" />
    <AdditionalInfoModal v-if="showAdditionalInfoModal" @closeModal="$store.dispatch('setShowAdditionalInfoModal', {value: false})" />
    </transition-group>
  </div>
</template>

<script>
import Navbar from '@/components/app/Navbar.vue'
import Sidebar from "@/components/app/Sidebar.vue";
import { defineComponent } from "@vue/runtime-core";
import Loader from '@/components/app/Loader.vue';
import UnderMaintenanceModal from "@/components/newModals/UnderMaintenanceModal.vue"
import BuyCoinPopupWindow from '@/components/app/BuyCoinPopupWindow.vue'
import PaymentCoinModule from '../components/app/PaymentCoinModule.vue';
import PaymentLevelModule from '../components/app/PaymentLevelModule.vue';
import OGCTVideoModal from "@/components/newModals/OGCTVideoModal.vue"
// import LiquidityVideoModal from "@/components/newModals/LiquidityVideoModal.vue"
import ExchangeBurningSection from '@/components/landing/ExchangeBurningSection.vue'
import { mapState } from "vuex";
import PaymentCryptostartModule from '../components/app/PaymentCryptostartModule.vue';
import PromoModal from '../components/newModals/PromoModal.vue'
import WithdrawOperationConfirmed from '../components/newModals/WithdrawOperationConfirmed.vue'
import AdditionalInfoModal from '../components/newModals/AdditionalInfoModal.vue';
import PlatformReviewsModal from '../components/newModals/PlatformReviewsModal.vue';

// INIT FIRESTORE

export default defineComponent({
  name: 'main-layout',
  data: () => ({
    isOpen: false,
    showPopup: false,
    showExchangeBurnViews: false,
    // showLiquidityVideoModal: false,
    showPromoModal: false,
    showWithdrawOperationConfirmed:false,
    showPlatformReviewsModal: false,
  }),
  components: {
    Sidebar,
    Navbar,
    Loader,
    UnderMaintenanceModal,
    BuyCoinPopupWindow,
    PaymentCoinModule,
    PaymentLevelModule,
    OGCTVideoModal,
    ExchangeBurningSection,
    // LiquidityVideoModal,
    PaymentCryptostartModule,
    PromoModal,
    WithdrawOperationConfirmed,
    AdditionalInfoModal,
    PlatformReviewsModal
},
  computed: {
    ...mapState("loader", ["loading"]),
    popupCoin() {
      return this.$store.getters.popupCoin
    },
    showPaymentCoinModule() {
      return this.$store.getters.showPaymentCoinModule
    },
    showPaymentLevelModule() {
      return this.$store.getters.showPaymentLevelModule
    },
    showPaymentCryptostartModule() {
      return this.$store.getters.showPaymentCryptostartModule
    },
    showOGCTVideoModal() {
      return this.$store.getters.showOGCTVideoModal
    },
    exchangeBurnTimeLeft(){
      return this.$store.state.info.exchangeBurnTimeLeft
    },
    withdrawSuccessfullUserIds() {
      return this.$store.getters.withdrawSuccessfullUserIds
    },
    showAdditionalInfoModal() {
      return this.$store.getters.showAdditionalInfoModal
    },
  },
  methods: {
    closeSidebar() {
      this.isOpen = false
    },
    forceUpdateAll() {
      // this.$forceUpdate()
      // this.$router.go()
      // window.location.reload()
    },
    isCurrentLocationBonus(){
      // console.log(location.href)
      // console.log(location.href.split(location.host)[1])
      // console.log(location.href.split(location.host)[1].includes('bonusAccount'))
      return (location.href.split(location.host)[1].includes('bonusAccount'))
    },
    async buyCoin(coinTicker) {
      await this.$store.dispatch('setPaymentCoinTicker', {coinTicker: coinTicker})
      this.$nextTick(()=>{
        this.showPopup=false
        this.$store.dispatch('setShowPaymentCoinModule', {value: true})
      })
    },
  },
  watch: {
        exchangeBurnTimeLeft(newVal, oldVal) {
           this.$nextTick(() => {
              this.showExchangeBurnViews = (newVal > 0) && !this.isCurrentLocationBonus();
           });
        },
    },
  async mounted() {
      this.showExchangeBurnViews = (this.exchangeBurnTimeLeft > 0) && !this.isCurrentLocationBonus()
      try {
        const user = await this.$store.dispatch('getProfile')

        if(user?.needToLinkTelegram == true){
          this.$router.push('/cabinet')
        } else if (user.buyCoinTicker && !(this.$store.getters.paymentCoinTicker)) {
          if (user.buyCoinTicker === 'OGCT') {
            await this.$store.dispatch('setShowOGCTVideoModal', {value: true})
          } else {
            this.buyCoin(user.buyCoinTicker)
          }
        }

        // } else {
				if (this.withdrawSuccessfullUserIds.includes(user.id) && localStorage.getItem("withdrawSuccesfulModalShown")===null) {
						setTimeout(()=>{
							this.showWithdrawOperationConfirmed=true
						},1000)
						localStorage.withdrawSuccesfulModalShown=true
				} else if (localStorage.getItem("promoModalTimestamp")===null || (+Date.now() - localStorage.promoModalTimestamp >=(12*60*60000))){
						setTimeout(()=>{
							this.showPromoModal=true
						},1000)
						localStorage.promoModalTimestamp=+Date.now()
				} else if (localStorage.getItem("platformReviewsShowTimestamp")===null){
						setTimeout(()=>{
							this.showPlatformReviewsModal=true
						},1000)
						localStorage.platformReviewsShowTimestamp=+Date.now()
				} 
		  // }
      } catch(e) {
          this.$error(e)
      }
  },
});
</script>

