  }

  .section-text {
    font-size: 16px;
    line-height: 19px;
  }

  .app-content {
    padding-left:0;
  }

  /* .sidebar{
      width:400px;
      -webkit-transform:translateX(-400px);
      transform:translateX(-400px);
  }

  .sidebar.open {
    -webkit-transform:translateX(0);
    transform:translateX(0);
  } */

  .cross {
    margin-left: 20px;
    visibility: visible;
  }

  .link-to-sidebar {
    visibility: visible;
  }

  .navbar {
    visibility: visible;
  }
  .app-content {
    margin-top: 80px;
  }
}

